<template><div class="markdown-body"><h1>底部表单</h1>
<p><code v-pre="">mu-bottom-sheet</code> 是一个从屏幕底部边缘向上滑出的一个面板，使用这种方式向用户呈现一组功能。呈现了简单、清晰、无需额外解释的一组操作。</p>
<h2 id="shi-li"><a class="header-anchor" href="#shi-li" aria-hidden="true">¶</a> 示例</h2>
<demo-block :jsfiddle="{&quot;html&quot;:&quot;&lt;mu-container&gt;\n  &lt;mu-flex justify-content=\&quot;center\&quot;&gt;\n    &lt;mu-button @click=\&quot;openBotttomSheet\&quot;&gt;OPEN Bottom Sheet&lt;/mu-button&gt;\n  &lt;/mu-flex&gt;\n  &lt;mu-bottom-sheet :open.sync=\&quot;open\&quot;&gt;\n    &lt;mu-list @item-click=\&quot;closeBottomSheet\&quot;&gt;\n      &lt;mu-sub-header&gt;Select One&lt;/mu-sub-header&gt;\n      &lt;mu-list-item button&gt;\n        &lt;mu-list-item-action&gt;\n          &lt;mu-icon value=\&quot;grade\&quot; color=\&quot;orange\&quot;&gt;&lt;/mu-icon&gt;\n        &lt;/mu-list-item-action&gt;\n        &lt;mu-list-item-title&gt;Star&lt;/mu-list-item-title&gt;\n      &lt;/mu-list-item&gt;\n      &lt;mu-list-item button&gt;\n        &lt;mu-list-item-action&gt;\n          &lt;mu-icon value=\&quot;inbox\&quot; color=\&quot;blue\&quot;&gt;&lt;/mu-icon&gt;\n        &lt;/mu-list-item-action&gt;\n        &lt;mu-list-item-title&gt;Inbox&lt;/mu-list-item-title&gt;\n      &lt;/mu-list-item&gt;\n      &lt;mu-list-item button&gt;\n        &lt;mu-list-item-action&gt;\n          &lt;mu-icon value=\&quot;chat\&quot; color=\&quot;green\&quot;&gt;&lt;/mu-icon&gt;\n        &lt;/mu-list-item-action&gt;\n        &lt;mu-list-item-title&gt;Chat&lt;/mu-list-item-title&gt;\n      &lt;/mu-list-item&gt;\n      &lt;mu-list-item button&gt;\n        &lt;mu-list-item-action&gt;\n          &lt;mu-icon value=\&quot;email\&quot; color=\&quot;cyan\&quot;&gt;&lt;/mu-icon&gt;\n        &lt;/mu-list-item-action&gt;\n        &lt;mu-list-item-title&gt;Email&lt;/mu-list-item-title&gt;\n      &lt;/mu-list-item&gt;\n    &lt;/mu-list&gt;\n  &lt;/mu-bottom-sheet&gt;\n&lt;/mu-container&gt;\n&quot;,&quot;script&quot;:null,&quot;style&quot;:null}">
                    <div class="source" slot="source"><mu-container>
  <mu-flex justify-content="center">
    <mu-button @click="openBotttomSheet">OPEN Bottom Sheet</mu-button>
  </mu-flex>
  <mu-bottom-sheet :open.sync="open">
    <mu-list @item-click="closeBottomSheet">
      <mu-sub-header>Select One</mu-sub-header>
      <mu-list-item button="">
        <mu-list-item-action>
          <mu-icon value="grade" color="orange"></mu-icon>
        </mu-list-item-action>
        <mu-list-item-title>Star</mu-list-item-title>
      </mu-list-item>
      <mu-list-item button="">
        <mu-list-item-action>
          <mu-icon value="inbox" color="blue"></mu-icon>
        </mu-list-item-action>
        <mu-list-item-title>Inbox</mu-list-item-title>
      </mu-list-item>
      <mu-list-item button="">
        <mu-list-item-action>
          <mu-icon value="chat" color="green"></mu-icon>
        </mu-list-item-action>
        <mu-list-item-title>Chat</mu-list-item-title>
      </mu-list-item>
      <mu-list-item button="">
        <mu-list-item-action>
          <mu-icon value="email" color="cyan"></mu-icon>
        </mu-list-item-action>
        <mu-list-item-title>Email</mu-list-item-title>
      </mu-list-item>
    </mu-list>
  </mu-bottom-sheet>
</mu-container>
</div>
                    
                    <div class="highlight" slot="highlight"><pre v-pre="" class="hljs"><code v-pre=""><span class="hljs-tag">&lt;<span class="hljs-name">mu-container</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">mu-flex</span> <span class="hljs-attr">justify-content</span>=<span class="hljs-string">"center"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">mu-button</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"openBotttomSheet"</span>&gt;</span>OPEN Bottom Sheet<span class="hljs-tag">&lt;/<span class="hljs-name">mu-button</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">mu-flex</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">mu-bottom-sheet</span> <span class="hljs-attr">:open.sync</span>=<span class="hljs-string">"open"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">mu-list</span> <span class="hljs-attr">@item-click</span>=<span class="hljs-string">"closeBottomSheet"</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">mu-sub-header</span>&gt;</span>Select One<span class="hljs-tag">&lt;/<span class="hljs-name">mu-sub-header</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">mu-list-item</span> <span class="hljs-attr">button</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">mu-list-item-action</span>&gt;</span>
          <span class="hljs-tag">&lt;<span class="hljs-name">mu-icon</span> <span class="hljs-attr">value</span>=<span class="hljs-string">"grade"</span> <span class="hljs-attr">color</span>=<span class="hljs-string">"orange"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">mu-icon</span>&gt;</span>
        <span class="hljs-tag">&lt;/<span class="hljs-name">mu-list-item-action</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">mu-list-item-title</span>&gt;</span>Star<span class="hljs-tag">&lt;/<span class="hljs-name">mu-list-item-title</span>&gt;</span>
      <span class="hljs-tag">&lt;/<span class="hljs-name">mu-list-item</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">mu-list-item</span> <span class="hljs-attr">button</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">mu-list-item-action</span>&gt;</span>
          <span class="hljs-tag">&lt;<span class="hljs-name">mu-icon</span> <span class="hljs-attr">value</span>=<span class="hljs-string">"inbox"</span> <span class="hljs-attr">color</span>=<span class="hljs-string">"blue"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">mu-icon</span>&gt;</span>
        <span class="hljs-tag">&lt;/<span class="hljs-name">mu-list-item-action</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">mu-list-item-title</span>&gt;</span>Inbox<span class="hljs-tag">&lt;/<span class="hljs-name">mu-list-item-title</span>&gt;</span>
      <span class="hljs-tag">&lt;/<span class="hljs-name">mu-list-item</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">mu-list-item</span> <span class="hljs-attr">button</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">mu-list-item-action</span>&gt;</span>
          <span class="hljs-tag">&lt;<span class="hljs-name">mu-icon</span> <span class="hljs-attr">value</span>=<span class="hljs-string">"chat"</span> <span class="hljs-attr">color</span>=<span class="hljs-string">"green"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">mu-icon</span>&gt;</span>
        <span class="hljs-tag">&lt;/<span class="hljs-name">mu-list-item-action</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">mu-list-item-title</span>&gt;</span>Chat<span class="hljs-tag">&lt;/<span class="hljs-name">mu-list-item-title</span>&gt;</span>
      <span class="hljs-tag">&lt;/<span class="hljs-name">mu-list-item</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">mu-list-item</span> <span class="hljs-attr">button</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">mu-list-item-action</span>&gt;</span>
          <span class="hljs-tag">&lt;<span class="hljs-name">mu-icon</span> <span class="hljs-attr">value</span>=<span class="hljs-string">"email"</span> <span class="hljs-attr">color</span>=<span class="hljs-string">"cyan"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">mu-icon</span>&gt;</span>
        <span class="hljs-tag">&lt;/<span class="hljs-name">mu-list-item-action</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">mu-list-item-title</span>&gt;</span>Email<span class="hljs-tag">&lt;/<span class="hljs-name">mu-list-item-title</span>&gt;</span>
      <span class="hljs-tag">&lt;/<span class="hljs-name">mu-list-item</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">mu-list</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">mu-bottom-sheet</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">mu-container</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2 id="bottom-sheet-props"><a class="header-anchor" href="#bottom-sheet-props" aria-hidden="true">¶</a> Bottom Sheet Props</h2>
<table>
<thead>
<tr>
<th>参数</th>
<th>介绍</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>open</td>
<td>是否打开</td>
<td>Boolean</td>
<td>—</td>
<td>false</td>
</tr>
<tr>
<td>overlay</td>
<td>是否有遮盖层</td>
<td>Boolean</td>
<td>—</td>
<td>true</td>
</tr>
<tr>
<td>overlay-close</td>
<td>点击遮盖层是否可以关闭</td>
<td>Boolean</td>
<td>—</td>
<td>true</td>
</tr>
<tr>
<td>overlay-opacity</td>
<td>遮盖层的透明度</td>
<td>Boolean</td>
<td>—</td>
<td>0.4</td>
</tr>
<tr>
<td>overlay-color</td>
<td>遮盖层的颜色</td>
<td>String</td>
<td>—</td>
<td>#000</td>
</tr>
<tr>
<td>esc-press-close</td>
<td>按下 <code v-pre="">esc</code> 键是否可以关闭</td>
<td>Boolean</td>
<td>—</td>
<td>true</td>
</tr>
<tr>
<td>append-body</td>
<td>弹出层是否添加到 body 元素后, 内部使用</td>
<td>Boolean</td>
<td>—</td>
<td>true</td>
</tr>
</tbody>
</table>
<h2 id="bottom-sheet-events"><a class="header-anchor" href="#bottom-sheet-events" aria-hidden="true">¶</a> Bottom Sheet Events</h2>
<table>
<thead>
<tr>
<th>名称</th>
<th>介绍</th>
<th>回调参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>close</td>
<td>当点击遮盖层或者按 esc 键，需要关闭弹层时触发</td>
<td>(reason) reason 关闭的原因  <br> <code v-pre="">overlay</code> : 点击了遮盖层 <br> <code v-pre="">esc</code> 按下 esc 键</td>
</tr>
</tbody>
</table>

</div class="markdown-body"></template>

<script>
export default {
  data () {
    return {
      open: false
    }
  },
  methods: {
    closeBottomSheet () {
      this.open = false;
    },
    openBotttomSheet () {
      this.open = true;
    }
  }
}
</script>


